import React from 'react';
import Detail from './Detail';

function DetailList(props){
	let details = props.filteredDetails.map((detail , i)=>{
		return <Detail key={i} name={detail.name}  breed={detail.breed} nopemesanan={detail.nopemesanan} asal={detail.asal} negaraAsal={detail.negaraAsal} tujuan={detail.tujuan} negaraTujuan={detail.negaraTujuan} ETA={detail.ETA} status={detail.status} tipe={detail.tipe} tipeKargo={detail.tipeKargo} incoterms={detail.incoterms}  tipeKontainer={detail.tipeKontainer}  />

	})
	return(
		<div>
			{details}
		</div>
	)
}

export default DetailList;